import {isWebpSupported, executeAndDebounce} from "./modules/functions.js";
isWebpSupported();

function stickyHeader(){
  const header = document.querySelector('.header')
  const scrolledClassName = '_scrolled'
  function foo(){
    if(window.scrollY > 0) {
      header.classList.add(scrolledClassName)
    } else {
      header.classList.remove(scrolledClassName)
    }
  }
  window.addEventListener('scroll', executeAndDebounce(foo, 200))
}
stickyHeader()

