const selector = '.signals__chart'
new LazyLoad({
  elements_selector: selector,
  unobserve_entered: true,
  callback_enter: () => {
    import("./modules/chart.js")
      .then(module => module.chart())
      .catch(e => console.log('Failed to load charts', e))
  }
})

new LazyLoad({
  elements_selector: ".signup",
  unobserve_entered: true,
  callback_enter: () => {
    const perkIcons = document.querySelectorAll('.perk__icons')
    for(const perkIcon of perkIcons) {
      const clone = perkIcon.querySelector('img').cloneNode()
      clone.classList.add('perk__icon-shadow')
      perkIcon.insertAdjacentElement('beforeend', clone)
    }
    lazyLoadInstance.update()
  }
})

